import axios from 'axios'

const base = window.location.protocol + '//' + window.location.host

const DeleteTransaction = (id, user) =>
  axios.post(base + '/api/admin/transaction/delete', { id, user })
const CreateTransaction = body =>
  axios.post(base + '/api/admin/transaction/create', body)
const DeleteDeposit = (id, user) =>
  axios.post(base + '/api/admin/deposit/delete', { id, user })
const RequestDeposit = body =>
  axios.post(base + '/api/wallet/deposit/create', body)
const GetAllDeposits = () => axios.get(base + '/api/admin/deposits')

export {
  DeleteTransaction,
  CreateTransaction,
  DeleteDeposit,
  RequestDeposit,
  GetAllDeposits,
}
